/* eslint-disable */
import React, { Component } from "react"
import { graphql } from "gatsby"
// import { Helmet } from "react-helmet"
import BehindTheScenes from "../components/Careers/BehindTheScenes/index"
import Advantages from "../components/Careers/Advantages/index"
import EmployeeStories from "../components/Careers/EmployeeStories/index"
import SearchBar from "../components/Careers/searchBar/searchBar"
import Slider from "react-slick"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { CareerDepartment } from "../components/Careers/CareerDepartment/CareerDepartment"
import CountBanner from "../components/Careers/CountBanner"

class Careers extends Component {
  constructor(props) {
    super(props)

    this.state = {
      keyword: "",
      team: "",
      location: "",
      department: "",
      locationName: "All",
      inputText: "",
      places: [],
      searchResult: {
        result: [],
        options: [],
      },
      jobResponseData: null,
    }
  }

  componentDidMount() {
    const { edges: response } = this.props.data.allJobsJson
    const uniquePlaces = new Set(
      response.map(res => res.node.categories.location)
    )
    const places = []
    for (let entry of uniquePlaces) {
      places.push(entry)
    }
    this.setState({
      jobResponseData: response,
      places,
    })
  }

  onChangeInputText = ev => {
    const targetvalue = ev.target.value
    this.setState(
      {
        inputText: targetvalue,
        searchResult: {
          ...this.state.searchResult,
          result: this.state.jobResponseData.filter((data) => {
            if (
              data.node.text.toLowerCase().includes(targetvalue.toLowerCase()) &&
              targetvalue !== '' &&
              ((this.state.locationName &&
                this.state.locationName.toLowerCase() === 'all') ||
                data.node.categories.location === this.state.locationName)
            ) {
              return data
            }
          }),
        },
      },
      () => {
        if (
          this.state.searchResult.result.length === 0 &&
          this.state.locationName !== 'All'
        ) {
          this.setState({
            searchResult: {
              ...this.state.searchResult,
              options: this.state.jobResponseData.filter((data, i) => {
                if (
                  data.node.text.toLowerCase().includes(targetvalue.toLowerCase()) &&
                  targetvalue !== ''
                ) {
                  return data
                }
              }),
            },
          })
        }
      }
    )
  }

  resetFilters = () => {
    this.setState({
      keyword: "",
      location: "",
      team: "",
      department: "",
    })
  }

  // eslint-disable-next-line
  onClickLocation = name => {
    this.setState(
      {
        locationName: name,
        searchResult: {
          ...this.state.searchResult,
          result: this.state.jobResponseData.filter((data, i) => {
            if (
              data.node.text
                .toLowerCase()
                .toLowerCase()
                .includes(this.state.inputText.toLowerCase()) &&
              this.state.inputText !== '' &&
              (data.node.categories.location.toLowerCase() === name.toLowerCase() ||
                name.toLowerCase() === 'all')
            ) {
              return data
            }
          }),
        },
      },
      () => {
        if (
          this.state.searchResult.result.length === 0 &&
          this.state.locationName !== 'All'
        ) {
          this.setState({
            searchResult: {
              ...this.state.searchResult,
              options: this.state.jobResponseData.filter((data, i) => {
                if (
                  data.node.text
                    .toLowerCase()
                    .includes(this.state.inputText.toLowerCase()) &&
                  this.state.inputText !== ''
                ) {
                  return data
                }
              }),
            },
          })
        }
      }
    )
  }

  componentWillMount() {
    if (typeof localStorage !== `undefined`) {
      localStorage.setItem("source", "gojek.io")
    }
  }

  render() {
    var settings = {
      infinite: true,
      centerMode: true,
      slidesToShow: 1,
      slidesToScroll: 1,
      centerPadding: "0px",
      dots: true,

      responsive: [
        {
          breakpoint: 768,
          settings: {
            dots: true,
            arrows: true,
            infinite: true,
            centerMode: true,
            slidesToShow: 1,
            autoplay: true,
            autoplaySpeed: 3000,
            slidesToScroll: 1,
          },
        },
        {
          breakpoint: 480,
          settings: {
            dots: true,
            arrows: true,
            infinite: true,
            centerMode: true,
            slidesToShow: 1,
            autoplay: true,
            autoplaySpeed: 3000,
            slidesToScroll: 1,
          },
        },
      ],
    }

    const sliderData = [
      {
        imgsrc: "career-banner-1",
      },
      {
        imgsrc: "career-banner-2",
      },
      {
        imgsrc: "career-banner-3",
      },
      {
        imgsrc: "career-banner-4",
      },
      {
        imgsrc: "career-banner-5",
      },
    ]
    return (
      <Layout location={this.props.location}>
        <SEO
          title="Gojek Careers: Check out the current job openings at Gojek Tech"
          description="Gojek is hiring the best and brightest of tech minds to build one of the world's most versatile and agile on-demand service apps."
        />
        <div className="first-section">
          {/* Main banner image */}
          <SearchBar
            props={this.props}
            places={this.state.places}
            searchResult={this.state.searchResult}
            onChangeInputText={ev => this.onChangeInputText(ev)}
            onClickLocation={name => this.onClickLocation(name)}
            locationName={this.state.locationName}
            inputText={this.state.inputText}
            textColor={`${
              this.state.searchResult !== null &&
              this.state.searchResult.length !== 0
                ? " text-dark "
                : " text-white "
            }`}
            source="gojek.io"
          />
          <CountBanner props={this.props} bannerImage="careers-second-banner" />
          <section>
            <div className="container-fluid pt-5 pt-md-1">
              <Slider
                {...settings}
                className="row go-jek-slider lg-controls-dark custom-controls-dark "
              >
                {sliderData.map((slide, i) => {
                  return (
                    <div key={i} className={slide.imgsrc}>
                      <div style={{ height: "50vh" }} />
                    </div>
                  )
                })}
              </Slider>
            </div>
          </section>

          <BehindTheScenes />

          <CareerDepartment props={this.props} />

          <Advantages />

          <EmployeeStories
            keyword={this.state.keyword}
            team={this.state.team}
            location={this.state.location}
            department={this.state.department}
            resetFilters={this.resetFilters.bind(this)}
            referer="https://www.gojek.io/"
            showHeadings={true}
          />
        </div>
      </Layout>
    )
  }
}

export default Careers

export const pageQuery = graphql`
  query JobQuery {
    allJobsJson {
      edges {
        node {
          id
          text
          description
          categories {
            commitment
            department
            location
            team
          }
        }
      }
    }
  }
`
